import React, { Component } from 'react';
import { Map, GoogleApiWrapper, Marker } from "google-maps-react";

const mapStyles = {
    width:'93%',
    height:'80%'
}

class GoogleMaps extends Component {

    constructor(props){
        super(props);
        this.state={
            lat:this.props.lat,
            lng:this.props.lng
        }
    }

    onMapClicked = (mapProps, map, event) => {
        this.setState({
            lat: event.latLng.lat(),
            lng: event.latLng.lng()
        }, this.props.callback({lat: event.latLng.lat(),lng: event.latLng.lng()}));
    }

    render() {
        return (
            <Map google={this.props.google} zoom={8} style={mapStyles} initialCenter={{ lat: this.state.lat, lng: this.state.lng}} onChange={this.onMapClicked} onClick={this.onMapClicked}>
                <Marker position={{ lat:this.state.lat, lng: this.state.lng}}/>
            </Map>
        );
    }
}

export default GoogleApiWrapper({
    apiKey: 'AIzaSyBs_GK2ph8FgRZNpLdK1Shiagcu8AIHB8g'
})(GoogleMaps);