import React from 'react';

const BenModal = ({ handleClose, handleOk, show, children, mapdata }) => {
    const showHideClassName = show ? "modal show display-block" : "modal display-none";

    return (
        <div className={showHideClassName}>
            <div className="modal-dialog">
                <div className="modal-content">
                    <div className="modal-header">
                        <h4 className="modal-title">Modal Heading</h4>
                        <button className="close" onClick={handleClose}>&times;</button>
                    </div>

                    <div className="modal-body w-100 text-center">
                        {children}
                    </div>

                    <div className="modal-footer">
                        <label>latitude: {mapdata.lat} </label> <label>longitude: {mapdata.lng}</label>
                        <button className="btn btn-primary" onClick={handleOk}>Ok</button>
                        <button className="btn btn-danger" onClick={handleClose}>Close</button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default BenModal;