import React, { Component } from 'react';
import { RestDataSource } from '../../webservice/RestDataSource';
import { ProductEditor } from './ProductEditor';

export class IsolatedEditor extends Component {

    constructor(props){
        super(props);
        this.state={
            dataItem:{}
        };
        this.dataSource = this.props.dataSource || new RestDataSource("http://localhost:3500/api/products");
        this.returnUrl = this.props.match.params.mode === "edit" ? this.props.match.url.substring(0, this.props.match.url.lastIndexOf("edit")-1) 
                                                                    : this.props.match.url.substring(0, this.props.match.url.lastIndexOf("/"));
        console.log(this.returnUrl);
    }

    save = (data) => {
        const callback = () => this.props.history.push(this.returnUrl);
        if(data.id === ""){
            this.dataSource.Store(data, callback);
        } else{
            this.dataSource.Update(data, callback);
        }
    }

    cancel = () => this.props.history.push(this.returnUrl);
    
    componentDidMount() {
        if(this.props.match.params.mode === "edit"){
            this.dataSource.GetOne(this.props.match.params.id, data => this.setState({ dataItem: data}));
        }
    }

    render() {
        return (
            <ProductEditor key={ this.state.dataItem.id }
                            product={ this.state.dataItem }
                            saveCallback={ this.save }
                            cancelCallback={ this.cancel } />
        );
    }
}
