import React, { Component } from 'react';
import { RestDataSource } from "../../webservice/RestDataSource";
import { Link } from "react-router-dom";
import GoogleMaps from '../gomap/GoogleMaps';
import { FaSearch, FaMapMarkedAlt } from 'react-icons/fa';
import BenModal from '../Modal/BenModal';

class WeatherInfoView extends Component {
    
    constructor(props){
        super(props);
        this.state = {
            weather: [],
            showModal: false,
            lat:-36.84846,
            lng:174.76333,
            city:'',
            hourly:{}
        }
        // this.dataSource = new RestDataSource("https://api.openweathermap.org/data/2.5/weather?q=Auckland&tunit=Celsius&appid=8e61f7cf97083a7e5730b91de326be77", (err) => this.props.history.push(`/components/error/${err}`));
        this.dataSource = new RestDataSource(`https://api.worldweatheronline.com/premium/v1/marine.ashx?key=6072b756bb2e48cfa6d40435201102&q=${this.state.lat},${this.state.lng}&format=json&tide=yes`, (err) => this.props.history.push(`/components/error/${err}`));
        this.dataSource2 = new RestDataSource("https://maps.googleapis.com/maps/api/geocode/json?address=1600+Amphitheatre+Parkway,+Mountain+View,+CA&key=AIzaSyBs_GK2ph8FgRZNpLdK1Shiagcu8AIHB8g", (err) => this.props.history.push(`/components/error/${err}`));
    }
    
    componentDidMount(){
        this.dataSource.GetData(data => this.reverseData(data.data));
    }

    showModal = () => {
        this.setState({showModal: true});
    }

    hideModal = () => {
        this.setState({showModal: false});
    }

    handleOk = () => {
        console.log(this.state.lat +"/"+ this.state.lng)
        this.dataSource.BASE_URL = `https://api.worldweatheronline.com/premium/v1/marine.ashx?key=6072b756bb2e48cfa6d40435201102&q=${this.state.lat},${this.state.lng}&format=json&tide=yes`;
        this.dataSource.GetData(data => this.reverseData(data.data));
    }

    reverseData = (data) => {
        console.log(data.weather[0]);
        let temp = data.weather[0].hourly[0];
        let obj = {};
        for (let key of Object.keys(temp)){
            let arr = [];
            for(let values of data.weather[0].hourly){
                if(Array.isArray(values[key]) && values[key].length > 0){
                    arr.push(values[key][0].value)
                }else{
                    arr.push(values[key]);
                }
            }
            obj[key] = (arr);
        }
        this.setState({ weather: data.weather, hourly: obj, showModal: false});
    }

    handleSearch = () => {   
        if(this.state.city !== ''){
            this.dataSource.BASE_URL = `https://geocoder.ls.hereapi.com/6.2/geocode.json?city=${this.state.city}&&&apiKey=Ee3v9zO8cz2_k7w9Ula80i0xAir0K56MZ24es6PQUbA`;
            this.dataSource.GetData(data => this.setState({ weather: data}));
        }
    }
    callback = (mapData) => {
        this.setState({lat: Number(mapData.lat).toFixed(5), lng: Number(mapData.lng).toFixed(5)})
    }

    handleOnChange = (event) => {
        console.log(event.target.value);
        this.setState({city:event.target.value});
    }

    getWeatherData = () => {
        return this.state.weather[0];
    }
    render() {
        return (
            <div className="container mt-3 mb-3">
                <div className="input-group md-form form-sm form-2 pl-0">
                    <input className="form-control my-0 py-1 amber-border" type="text" placeholder="Search City" onChange={event => this.handleOnChange(event)}/>
                    <div className="input-group-append">
                        <button className="input-group-text amber lighten-3" onClick={this.handleSearch}><FaSearch/></button>
                        <button className="input-group-text amber lighten-3" onClick={this.showModal}><FaMapMarkedAlt/></button>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-12">2020-2-12</div>
                </div>
                <div className="row">
                    <div className="col-sm-12">Astronomy
                        <div className="row">
                            <div className="col-sm-2 border">sunrise</div>
                            <div className="col-sm-2 border">sunset</div>
                            <div className="col-sm-2 border">moonrise</div>
                            <div className="col-sm-2 border">moonrise</div>
                            <div className="col-sm-2 border">moon_phase</div>
                            <div className="col-sm-2 border">moon_illumination</div>
                        </div>
                        <div className="row">
                            <div className="col-sm-2 border">06:48 AM</div>
                            <div className="col-sm-2 border">08:22 PM</div>
                            <div className="col-sm-2 border">10:31 PM</div>
                            <div className="col-sm-2 border">09:40 AM</div>
                            <div className="col-sm-2 border">Waning Gibbous</div>
                            <div className="col-sm-2 border">68</div>
                        </div>
                    </div>
                </div>

                <div className="row">
                    <div className="col-12">Temperature
                        <div className="row">
                            <div className="col-sm-3 border">maxtempC</div>
                            <div className="col-sm-3 border">maxtempF</div>
                            <div className="col-sm-3 border">mintempC</div>
                            <div className="col-sm-3 border">mintempF</div>
                        </div>
                        <div className="row">
                            <div className="col-sm-3 border">21</div>
                            <div className="col-sm-3 border">70</div>
                            <div className="col-sm-3 border">16</div>
                            <div className="col-sm-3 border">61</div>
                        </div>
                    </div>
                </div>

                <div className="row">
                    <div className="col-12">Tides
                        <div className="row">
                            <div className="col-sm-4 border">tideDateTime</div>
                            <div className="col-sm-4 border">tide_type</div>
                            <div className="col-sm-4 border">tideHeight_mt</div>
                        </div>
                        <div className="row">
                            <div className="col-sm-4 border">2020-02-12 04:17</div>
                            <div className="col-sm-4 border">LOW</div>
                            <div className="col-sm-4 border">0.18</div>
                        </div>
                        <div className="row">
                            <div className="col-sm-4 border">2020-02-12 10:44</div>
                            <div className="col-sm-4 border">HIGH</div>
                            <div className="col-sm-4 border">3.55</div>
                        </div>
                        <div className="row">
                            <div className="col-sm-4 border">2020-02-12 04:17</div>
                            <div className="col-sm-4 border">LOW</div>
                            <div className="col-sm-4 border">0.18</div>
                        </div>
                        <div className="row">
                            <div className="col-sm-4 border">2020-02-12 04:17</div>
                            <div className="col-sm-4 border">LOW</div>
                            <div className="col-sm-4 border">0.18</div>
                        </div>
                    </div>
                </div>

                <div className="row">
                    <div className="col-12">Hourly
                        <div className="row">
                            <div className="col-sm-4 border">Time</div>
                            {/* {this.state.hourly.map(p => console.log(p))}; */}
                            {this.state.hourly.hasOwnProperty('time') && this.state.hourly.time.map((p, index) => 
                                <div className="col-sm-1 border" key={index}>{p}</div>                                
                            )}
                        </div>
                        <div className="row">
                            <div className="col-sm-4 border">Temperature</div>
                            {this.state.hourly.hasOwnProperty('tempC') && this.state.hourly.tempC.map((p, index) => 
                                <div className="col-sm-1 border" key={index}>{p}</div>                                
                            )}
                        </div>
                        <div className="row">
                            <div className="col-sm-4 border">Wind Speed</div>
                            {this.state.hourly.hasOwnProperty('windspeedMiles') && this.state.hourly.windspeedMiles.map((p, index) => 
                                <div className="col-sm-1 border" key={index}>{p}Kmph</div>                                
                            )}
                        </div>
                        <div className="row">
                            <div className="col-sm-4 border">Wind Degree</div>
                            {this.state.hourly.hasOwnProperty('winddirDegree') && this.state.hourly.winddirDegree.map((p, index) => 
                                <div className="col-sm-1 border" key={index}>{p}</div>                                
                            )}
                        </div>
                        <div className="row">
                            <div className="col-sm-4 border">Weather Icon</div>
                            {this.state.hourly.hasOwnProperty('weatherIconUrl') && this.state.hourly.weatherIconUrl.map((p, index) => 
                                <div className="col-sm-1 border" key={index}><img src={p} /></div>                                
                            )}
                        </div>
                        <div className="row">
                            <div className="col-sm-4 border">Weather Desc</div>
                            {this.state.hourly.hasOwnProperty('weatherDesc') && this.state.hourly.weatherDesc.map((p, index) => 
                                <div className="col-sm-1 border" key={index}>{p}</div>                                
                            )}
                        </div>
                    </div>
                </div>
               
                <BenModal show={this.state.showModal} mapdata={{lat: this.state.lat,lng: this.state.lng}} handleClose={this.hideModal} handleOk={this.handleOk}>
                    <GoogleMaps lat={this.state.lat} lng={this.state.lng} isMarkerShown callback={mapData => this.callback(mapData)}/>
                </BenModal>
            </div>
        );
    }
}

export default WeatherInfoView;